:root {
  --font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
  --font-size-xs: .75rem;
  --font-size-sm: .875rem;
  --font-size-base: 1rem;
  --font-size-lg: 1.125rem;
  --font-size-xl: 1.25rem;
  --color-background: #0a0a0a;
  --color-surface: #666;
  --color-surface-hover: #777;
  --color-surface-active: #333;
  --color-input: #999;
  --color-primary: #00baff;
  --color-primary-hover: #33c8ff;
  --color-secondary: #fd0;
  --color-accent: #ed7e11;
  --color-text: #fefefe;
  --color-text-muted: #ccc;
  --color-text-dark: #333;
  --color-border: #555;
  --color-toggle-off: #ccc;
  --color-toggle-on: #fff;
  --color-notice-bg: #ffffffe6;
  --color-notice-header: #fd0;
  --color-toast-bg: #ccc;
  --color-copyright: white;
  --space-xs: .25rem;
  --space-sm: .5rem;
  --space-md: 1rem;
  --space-lg: 1.5rem;
  --space-xl: 2rem;
  --radius-sm: 5px;
  --radius-md: 10px;
  --radius-lg: 20px;
  --radius-full: 9999px;
  --transition-fast: .15s ease;
  --transition-base: .25s ease;
  --transition-slow: .5s ease;
  --shadow-sm: 0 2px 4px #0000001a;
  --shadow-md: 0 4px 12px #00000026;
  --shadow-lg: 0 8px 24px #0003;
}

:root.light {
  --color-background: #f5f5f5;
  --color-surface: #e0e0e0;
  --color-surface-hover: #d0d0d0;
  --color-surface-active: silver;
  --color-input: #fff;
  --color-text: #333;
  --color-text-muted: #666;
  --color-border: #ccc;
  --color-copyright: black;
}

* {
  box-sizing: border-box;
}

body {
  font-family: var(--font-family);
  font-size: var(--font-size-base);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
}

canvas {
  width: 100%;
  height: 100%;
  position: fixed;
  inset: 0;
}

div.copyright {
  color: var(--color-copyright);
  font-size: var(--font-size-xs);
  z-index: 999;
  text-align: center;
  width: 90%;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

div.copyright a {
  color: var(--color-copyright);
}

.icon {
  vertical-align: middle;
  background: currentColor;
  width: 25px;
  height: 25px;
  display: inline-block;
  mask-position: center;
  mask-size: contain;
  mask-repeat: no-repeat;
}

.icon.play {
  mask-image: url("play.d2b98249.svg");
}

.icon.shuffle {
  mask-image: url("shuffle.e9e498cf.svg");
}

.icon.megaphone {
  mask-image: url("megaphone.da49dfc8.svg");
}

.icon.record {
  mask-image: url("record.c71c4728.svg");
}

.icon.map {
  mask-image: url("map.eb357c03.svg");
}

.icon.trophy {
  mask-image: url("trophy.50d1ba0a.svg");
}

.icon.bomb {
  mask-image: url("bomb.455f9b17.svg");
}

.icon.sun {
  mask-image: url("sun.ab6ec46d.svg");
}

.icon.moon {
  mask-image: url("moon.3f475bd2.svg");
}

#settings {
  bottom: var(--space-md);
  left: var(--space-md);
  background: var(--color-surface);
  border-radius: var(--radius-md);
  padding: var(--space-md);
  z-index: 999;
  visibility: visible;
  opacity: 1;
  min-width: 50%;
  transition: visibility 0s, opacity var(--transition-slow);
  display: flex;
  position: fixed;
}

#settings.hide {
  opacity: 0;
  visibility: hidden;
}

#settings h3 {
  font-size: var(--font-size-sm);
  color: var(--color-text);
  margin: 0;
  padding: 0;
}

#settings textarea {
  background: var(--color-input);
  width: 100%;
  min-height: 5rem;
  font-family: var(--font-family);
  font-size: var(--font-size-sm);
  border-radius: var(--radius-sm);
  padding: var(--space-sm);
  border: none;
}

#settings button {
  color: var(--color-text);
  background: var(--color-surface-active);
  border-radius: var(--radius-sm);
  padding: var(--space-sm) var(--space-md);
  font-family: var(--font-family);
  font-size: var(--font-size-sm);
  cursor: pointer;
  transition: background var(--transition-fast);
  border: none;
  position: relative;
  overflow: hidden;
}

#settings button:hover {
  background: var(--color-surface-hover);
}

#settings button:active:after {
  content: "";
  background: #00000080;
  position: absolute;
  inset: 0;
}

#settings div.theme {
  color: var(--color-text);
  flex-grow: 1;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

#settings div.left {
  flex-grow: 1;
  flex-shrink: 1;
  order: 1;
}

#settings div.left .actions {
  justify-content: stretch;
  align-items: center;
  gap: var(--space-xs);
  display: flex;
}

#settings div.left .actions div.sep {
  flex-grow: 1;
}

#settings div.right {
  flex-grow: 0;
  flex-shrink: 0;
  order: 2;
}

#settings div.right div.row {
  align-items: center;
  height: 35px;
  display: flex;
}

#settings div.right div.row label {
  width: 150px;
  padding-left: var(--space-md);
  color: var(--color-text);
  font-size: var(--font-size-sm);
  flex-grow: 0;
  flex-shrink: 0;
}

#settings select {
  border-radius: var(--radius-sm);
  background: var(--color-input);
  width: 100%;
  height: 25px;
  font-family: var(--font-family);
  font-size: var(--font-size-sm);
  cursor: pointer;
  border: none;
}

#settings input[type="checkbox"] {
  vertical-align: middle;
  cursor: pointer;
  width: 50px;
  height: 25px;
  padding-right: 63px;
  display: inline-block;
  position: relative;
}

#settings input[type="checkbox"]:before {
  content: "";
  border-radius: var(--radius-full);
  background: var(--color-input);
  width: 50px;
  height: 25px;
  transition: background var(--transition-fast);
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
}

#settings input[type="checkbox"]:after {
  content: "";
  border-radius: var(--radius-full);
  background: var(--color-toggle-off);
  width: 25px;
  height: 25px;
  transition: transform var(--transition-fast), background var(--transition-fast);
  position: absolute;
  top: 0;
  left: 0;
}

#settings input[type="checkbox"]:checked:after {
  background: var(--color-toggle-on);
  transform: translateX(100%);
}

#settings input[type="checkbox"]:checked:before {
  content: "";
  background: var(--color-primary);
}

#settings .btn-group {
  justify-content: stretch;
  display: flex;
}

#settings .btn-group > * {
  box-sizing: border-box;
  background: var(--color-input);
  width: 33%;
  height: 25px;
  color: var(--color-text);
  font-family: var(--font-family);
  font-size: var(--font-size-sm);
  cursor: pointer;
  transition: background var(--transition-fast);
  border: none;
  border-radius: 0;
  flex-grow: 0;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  overflow: hidden;
}

#settings .btn-group > :first-child {
  border-radius: var(--radius-md) 0 0 var(--radius-md);
}

#settings .btn-group > :last-child {
  border-radius: 0 var(--radius-md) var(--radius-md) 0;
}

#settings .btn-group > :hover {
  background: var(--color-surface-hover);
}

#settings .btn-group > .active:before {
  content: "";
  vertical-align: middle;
  background: var(--color-text);
  width: 15px;
  height: 15px;
  display: inline-block;
  mask-image: url("check.7e390469.svg");
  mask-repeat: no-repeat;
}

#settings .btn-group > .active {
  background: var(--color-surface-active);
}

#settings .btn-group input[type="number"] {
  box-sizing: border-box;
  text-align: center;
  font-family: var(--font-family);
}

#notice {
  background: var(--color-notice-bg);
  border-radius: var(--radius-lg);
  z-index: 1001;
  width: 500px;
  max-width: 90%;
  padding: var(--space-md);
  color: var(--color-text-dark);
  box-shadow: var(--shadow-lg);
  flex-direction: column;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

#notice h1 {
  background-color: var(--color-notice-header);
  margin: calc(var(--space-md) * -1) calc(var(--space-md) * -1) 0 calc(var(--space-md) * -1);
  padding: var(--space-md) var(--space-sm) var(--space-md) var(--space-sm);
  border-bottom: 1px solid var(--color-text-dark);
  font-size: var(--font-size-lg);
  align-items: center;
  display: flex;
}

#notice h1:before {
  content: "";
  width: 1em;
  height: 1em;
  margin-right: var(--space-xs);
  background-image: url("megaphone.da49dfc8.svg");
  background-size: contain;
  display: inline-block;
}

#notice div.notice-body {
  padding: var(--space-md) var(--space-sm);
  font-size: var(--font-size-sm);
}

#notice div.notice-action {
  justify-content: end;
  display: flex;
}

#notice div.notice-action button {
  color: var(--color-text);
  background: var(--color-surface-active);
  border-radius: var(--radius-lg);
  padding: var(--space-sm) var(--space-md);
  font-family: var(--font-family);
  font-size: var(--font-size-sm);
  cursor: pointer;
  width: 50%;
  height: 50px;
  transition: background var(--transition-fast);
  border: none;
  position: relative;
  overflow: hidden;
}

#notice div.notice-action button:hover {
  background: var(--color-surface-hover);
}

#notice div.notice-action button:active:after {
  content: "";
  background: #00000080;
  position: absolute;
  inset: 0;
}

#donate {
  bottom: calc(160px + var(--space-lg));
  left: var(--space-md);
  z-index: 999;
  visibility: visible;
  opacity: 1;
  transition: visibility 0s, opacity var(--transition-slow);
  display: flex;
  position: fixed;
}

#donate.hide {
  opacity: 0;
  visibility: hidden;
}

#donate .icon {
  width: 24px;
  height: 24px;
}

.recap-button.small {
  margin-left: var(--space-md);
}

@media screen and (width <= 750px) {
  #donate {
    width: calc(100% - var(--space-xl));
    bottom: 3rem;
  }

  #donate .bmc-btn-container {
    width: calc(100% - 70px);
  }

  #donate .bmc-btn-container .bmc-btn {
    width: 100%;
  }

  #donate .bmc-btn-container .bmc-btn-text {
    font-size: var(--font-size-sm);
  }

  #donate .recap-button.small {
    margin-left: var(--space-sm);
  }

  #settings {
    min-width: 0;
    max-width: 100%;
    width: calc(100% - var(--space-xl));
    opacity: 1;
    visibility: visible;
    transition: visibility 0s, opacity var(--transition-slow);
    display: block;
    bottom: calc(4rem + 60px);
    overflow: hidden;
  }

  #settings.hide {
    opacity: 0;
    visibility: hidden;
  }

  #settings div.right div.row {
    border-bottom: 1px solid var(--color-border);
    height: auto;
    padding: var(--space-sm) 0;
    display: block;
  }

  #settings div.right div.row label {
    width: 100%;
    margin-bottom: var(--space-sm);
    padding-left: 0;
    display: block;
  }

  #settings div.right div.row .icon {
    width: 15px;
    height: 15px;
  }

  #notice {
    box-sizing: border-box;
    background: var(--color-notice-bg);
    border-radius: var(--radius-sm);
    z-index: 1001;
    width: 100%;
    max-width: 100%;
    height: 100%;
    padding: var(--space-sm) var(--space-md);
    color: var(--color-text-dark);
    flex-direction: column;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    transform: none;
  }

  #notice div.notice-body {
    flex-grow: 1;
  }
}

div.toast {
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-sm);
  background: var(--color-toast-bg);
  color: var(--color-text-dark);
  font-family: var(--font-family);
  font-size: var(--font-size-sm);
  bottom: var(--space-md);
  z-index: 1000;
  box-shadow: var(--shadow-md);
  animation: var(--transition-fast) linear fade-in, var(--transition-fast) linear 1s reverse fade-in;
  position: fixed;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%);
}

@keyframes fade-in {
  0% {
    opacity: 0;
    transform: translate(-50%, 100%);
  }

  100% {
    opacity: 1;
    transform: translate(-50%);
  }
}

.link_donation {
  padding: var(--space-md);
  background: repeating-linear-gradient(45deg, var(--color-accent), var(--color-accent) 20px, var(--color-surface-active) 20px, var(--color-surface-active) 40px);
  width: 100%;
  color: var(--color-text);
  font-size: var(--font-size-lg);
  border-radius: var(--radius-sm);
  margin-bottom: var(--space-md);
  text-shadow: -2px 0 #000, 0 2px #000, 2px 0 #000, 0 -2px #000;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  text-decoration: none;
  display: flex;
}
